<template>
  <div>
      <div class="title">
           机械设备
      </div>
    <el-select v-model="value" @change='shijian' placeholder="请选择">
        <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
        </el-option>
    </el-select>
    <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
        prop="name"
        label="名字"
        width="180">
        </el-table-column>
        <el-table-column
        prop="v"
        label="当前值"
        width="180">
        </el-table-column>
        <el-table-column
        prop="ts"
        label="时间">
        </el-table-column>
    </el-table>
  </div>
</template>

<script>
import url from "../../common/api";
export default {
    data() {
      return {
        tableData: [],
        options: [{
            value: 300000,
            label: '五分钟'
            }, {
            value: 60000,
            label: '一分钟'
            }, {
            value: 30000,
            label: '三十秒'
            }, {
            value: 10000,
            label: '十秒'
        }],
        value: 60000,
        timer:''
      };
    },
    methods:{
        Fndevicelistv2(){//机械设备后台给url
            url.devicelistv2({
                id:this.$route.query.id
            }).then((res) => {
                console.log(res,'---hhhh---------机械设备后台给url--')
                url.Mechanics(res.data.url[0])
                .then(res=>{
                    console.log(res,'----hhhhhhh--------机械设备后台给url--')
                    this.tableData=res.data.feature.monitor
                })
            })
            this.FnInterval()
        },
        FnInterval(){
            console.log(this.value,'----------------时间')
            this.timer=setInterval(()=>{
                url.devicelistv2({
                    id:this.$route.query.id
                }).then((res) => {
                    console.log(res,'------------机械设备后台给url--')
                    url.Mechanics(res.data.url[0])
                    .then(res=>{
                        console.log(res,'----研华--------机械设备后台给url--')
                        this.tableData=res.data.feature.monitor
                    })
                })
            },this.value)

        },
        shijian(a){
            this.value=a
            console.log(a,this.value,'------------离开')
            clearInterval(this.timer)
            this.FnInterval()
        }
    },
    mounted(){
        this.Fndevicelistv2()//机械设备后台给url
    },
    beforeRouteLeave(to,from,next){
      clearInterval(this.timer)
      next()
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
</style>


